<template>
  <div class="inner">
    <div class="heading">
      <h1 class="title">{{ title }}</h1>
      <transition name="fade">
        <ul class="action" v-show="selections.length">
          <li><a href="#" class="icon-before icon-checkmark"></a></li>
          <li><a href="#" class="icon-before icon-blocked"></a></li>
          <li><a href="#" class="icon-before icon-bin"></a></li>
          <li><a href="#" class="icon-before icon-copy"></a></li>
        </ul>
      </transition>
      <div class="search icon-before icon-search">
        <input type="text" placeholder="搜索">
      </div>
      <router-link :to="{ name: 'new', params: { type: $route.params.type } }"><el-button type="primary" size="small" icon="edit">写文章</el-button></router-link>
    </div>
    <el-table :data="temp" @selection-change="handleSelectionChange">
      <el-table-column type="selection"></el-table-column>
      <el-table-column prop="date" label="日期" width="160"></el-table-column>
      <el-table-column prop="name" label="姓名" width="160"></el-table-column>
      <el-table-column prop="address" label="地址" show-overflow-tooltip></el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: 'posts',

    data () {
      const temp = []
      for (let i = 0; i < 100; i++) {
        temp.push({
          date: new Date().toLocaleDateString(),
          name: i % 2 ? '汪小六' : '汪小又',
          address: `北京市海淀区中关村国际软件大厦 ${i} 号`
        })
      }

      let title
      switch (this.$route.params.type) {
        case 'blog':
          title = `${temp.length} 篇博客`
          break
        case 'page':
          title = `${temp.length} 个页面`
          break
      }

      return {
        title: title,
        temp: temp,
        selections: []
      }
    },

    created () {
      this.$title(this.$route.params.type)
    },

    methods: {
      handleSelectionChange (value) {
        this.selections = value
        if (this.selections.length) {
          this.pretitle = this.title
          this.title = `${this.selections.length} 条被选中`
        } else {
          this.title = this.pretitle
        }
      }
    }
  }
</script>

<style>
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .2s ease;
  }

  .fade-enter,
  .fade-leave-active {
    opacity: 0;
  }
</style>



